import * as React from "react";
import { ProjectCard } from "./ProjectCard";
import { contributions, projects } from "../../projects";
import "./Projects.css";

export function Projects() {
  return (
    <>
      <section id="projects">
        <h2>
          <span className="text--outline">Pro</span>
          jects
        </h2>
        <div className="projects__list">
          {projects.map((project) => (
            <ProjectCard key={project.git} project={project} />
          ))}
        </div>
      </section>
      <section id="contributions">
        <h2>
          Con
          <span className="text--outline">trib</span>
          uted I<span className="text--outline">n</span>
        </h2>
        <div className="projects__list">
          {contributions.map((project) => (
            <ProjectCard key={project.git} project={project} />
          ))}
        </div>
      </section>
    </>
  );
}
