      <span style={{ textAlign: "left" }}>
        This is Mohit aka Cherry XO. Just stepped in this new World. Interested
        in ReactJs, MongoDB, Graphql. Open for Collabs{" "}
        <Emoji text="‚úå" description="victory hand emoji" />.
      </span>
