import * as React from "react";
import ExpressJSIcon from "../../assets/svg/expressjs.svg";
import {
  SiMongodb,
  SiReason,
  SiNodeDotJs,
  SiReact,
  SiGraphql,
} from "react-icons/si";
import "./Skills.css";

export function Skills() {
  return (
    <section id="skills">
      <h2>
        Preferred S<span className="text--outline">tack</span>
      </h2>
      <ul className="skills__list">
        <li className="skills__listItem">
          <SiMongodb className="skills__icon" />
          MongoDB
        </li>
        <li className="skills__listItem">
          <ExpressJSIcon className="skills__icon" />
          ExpressJS
        </li>
        <li className="skills__listItem">
          <SiReact className="skills__icon" />
          ReactJs
        </li>
        <li className="skills__listItem">
          <SiNodeDotJs className="skills__icon" />
          NodeJs
        </li>
        <li className="skills__listItem">
          <SiGraphql className="skills__icon" />
          Graphql
        </li>
        <li className="skills__listItem text--washed">
          <SiReason className="skills__icon" />
          Reason
        </li>
      </ul>
    </section>
  );
}
