import * as React from "react";
import { links } from "../../constants";
import "./Footer.css";

export function Footer() {
  return (
    <footer id="contacts">
      <h2 id="contacts">
        Lin
        <span className="text--outline">ks</span>
      </h2>
      <ul className="contacts__list">
        {links.map(({ Logo, href }) => (
          <li className="contacts__item">
            <a key={href} href={href}>
              <Logo className="icon icon--xl" />
            </a>
          </li>
        ))}
      </ul>
      <p className="copyright">
        Copyright &copy; by <a href="https://github.com/manu-xo5">Manu XO5</a>.
        2021
      </p>
    </footer>
  );
}
