import * as React from "react";
import { SiGithub as Github } from "react-icons/si";
import { FaGlobe as Web } from "react-icons/fa";

export function ProjectCard({ project }) {
  return (
    <article className="project-card">
      <p>{project.name}</p>

      <div className="project-card__iconList project-card__iconList--mb">
        {project.stack.map((Icon, idx) => (
          <Icon className="icon icon--sm" key={idx} />
        ))}
      </div>

      <div className="project-card__iconList project-card__iconList--right">
        <a className="project-card__link" href={project.git}>
          <Github className="icon icon--sm" />
        </a>
        {project.web ? (
          <a className="project-card__link" href={project.web}>
            <Web className="icon icon--sm" />
          </a>
        ) : null}
      </div>
    </article>
  );
}
